.skip {
  text-align: center;
  color: #fff;
  margin-top: 17.9466666667%;
  font-size: 2.6666666667vw;
  position: relative;
  padding-bottom: 9px;
}
.skip .arrow {
  width: 9px;
  height: 9px;
  border: 1px solid;
  border-color: transparent transparent #fff #fff;
  transform: translate(-50%, 0) rotate(-45deg);
  position: absolute;
  bottom: 0;
  left: 50%;
  opacity: 1;
  animation: arrow 3s infinite linear;
}

@keyframes arrow {
  0% {
    transform: translate(-50%, 0) rotate(-45deg);
    opacity: 1;
  }
  30% {
    transform: translate(-50%, 0) rotate(-45deg);
    opacity: 1;
  }
  80% {
    transform: translate(-50%, 20px) rotate(-45deg);
    opacity: 0;
  }
  81% {
    transform: translate(-50%, 0) rotate(-45deg);
    opacity: 0;
  }
  100% {
    transform: translate(-50%, 0) rotate(-45deg);
    opacity: 1;
  }
}
.overlay {
